<template>
  <div>
      <ul>
          <li v-for="(todoList, index) in todoLists" v-bind:key="todoList.item" class="shadow">
              <i class="fas fa-check checkBtn" :class="{checkBtnCompleted : todoList.completed}" v-on:click="toggleComplete(todoList,index)"></i>
              <span :class="{textCompleted : todoList.completed}">{{todoList.item}}</span>
              <span class="removeBtn" v-on:click="removeTodo(todoList,index)">
                  <i class="fas fa-trash-alt"></i>
              </span>
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    data: function(){
        return {
            todoLists:[],
        }
    },
    created : function(){
        for (var i = 0; i < localStorage.length; i++) {
            if (localStorage.key(i) !== 'loglevel:webpack-dev-server') {
                var convert = JSON.parse(localStorage.getItem(localStorage.key(i)));
                this.todoLists.push(convert);
                // this.todoLists.push(JSON.parse(localStorage.key(i)));
            }
    
        }
    },
    methods: {
        removeTodo: function(todoList,index) {
            localStorage.removeItem(todoList);
            this.todoLists.splice(index,1);
        },
        toggleComplete : function(todoList,index){
            console.log(index)
            todoList.completed = !todoList.completed;
            localStorage.setItem(todoList.item, JSON.stringify(todoList));
        }
    }
}
</script>

<style scoped>
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: left;
}
li {
  display: flex;
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background: white;
  border-radius: 5px;
}
.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 5px;
}
.checkBtnCompleted {
  color: #b3adad;
}
.textCompleted {
  text-decoration: line-through;
  color: #b3adad;
}
.removeBtn {
  margin-left: auto;
  color: #de4343;
}
</style>