<template>
  <div class="inputBox">
      <input type="text" v-model="newTodoItem" v-on:keyup.enter="addTodo">
      <span class="addContainer" v-on:click="addTodo">
          <i class="fas fa-plus addBtn"></i>
      </span>
  </div>
</template>

<script>
export default {
    data:function(){
        return {
            newTodoItem:'',
        }
    },
    methods : {
        addTodo : function(){
            console.log(this.newTodoItem);
            if (this.newTodoItem !== '') {
              var obj = {
                completed: false,
                item:this.newTodoItem,
              } 
              // localStorage.setItem(key,value); 로컬 스토리지 저장
              localStorage.setItem(this.newTodoItem,JSON.stringify(obj));
              this.clearInput();
            }
            
        },
        clearInput : function(){
            this.newTodoItem = '';
        }
    }
}
</script>

<style scoped>
input:focus {
  outline: none;
}
.inputBox {
  background: white;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
}
.inputBox input {
  border-style: none;
  font-size: 0.9rem;
  width: 80%;
  padding: 0 20px;
  background: #e9e9e9;
  height: 50px;
}
.addContainer {
  float: right;
  background: linear-gradient(to right, #6478FB, #8763FB);
  display: block;
  width: 3rem;
  border-radius: 0 5px 5px 0;
}
.addBtn {
  color: white;
  vertical-align: middle;
}
</style>